import React from 'react'

const Poruka = ({ poruka, uredi, ukloni }) => {  
   
  
  return (
    <li className="poruka list-group-item">
      {poruka.ime} ({poruka.email})

      <button onClick={uredi} className="btn btn-outline-secondary ml-4">Uredi</button>
      <button onClick={ukloni} className="btn btn-outline-danger ml-4">Ukloni</button>
    </li>
  )
  }

export default Poruka