import React, {useState, useEffect} from 'react'
import Poruka from './components/Poruka'
import porukeServer from './services/poruke'



const App = (props) => {
  const [ poruke, postaviPoruke] = useState([])
  const [ unosIme, postaviUnosIme] = useState('')
  const [ unosEmail, postaviUnosEmail] = useState('')

  const [ idNovo, postaviidNovo] = useState('')
  const [ unosImeNovo, postaviUnosImeNovo] = useState('')
  const [ unosEmailNovo, postaviUnosEmailNovo] = useState('')

  const [ unosImeFilter, postaviUnosImeFilter] = useState('')

  useEffect( () => {
      //console.log('effect hook');
      porukeServer.dohvatiSve()
      .then( (response) => { 
      //console.log("podaci ucitani");
      postaviPoruke(response.data)
   })} 
  ,[])
 
  
  let fporuke = filterItems(poruke, unosImeFilter);
  const porukeZaIspis = fporuke.length >= 0 ? fporuke: poruke
 
  const novaPoruka = (e) => {
    e.preventDefault()  
    //console.log('Klik ', e.target) 
    const noviObjekt = {
        //id: poruke.length + 1,  //ovaj dio radi server
        ime: unosIme,
        email: unosEmail  
      }

    porukeServer.stvori(noviObjekt)
    .then(
        (response) => {
            console.log(response.statusText);
            //console.log(response.status)
            postaviPoruke(poruke.concat(response.data)) 
            postaviUnosIme('')
            postaviUnosEmail('')
        }
    )
    .catch(error => {
      window.alert(error.response.data.error);
     })
}
  const promjenaUnosaIme = (e) => {
    //console.log(e.target.value);
    postaviUnosIme(e.target.value)
  }
  const promjenaUnosaEmail = (e) => {
    //console.log(e.target.value);
    postaviUnosEmail(e.target.value)
  }
  const promjenaUnosaImeNovo = (e) => {
    //console.log(e.target.value);
    postaviUnosImeNovo(e.target.value)
  }
  const promjenaidNovo = (e) => {
    //console.log(e.target.value);
    postaviidNovo(e.target.value)
  }
  const promjenaUnosaEmailNovo = (e) => {
    //console.log(e.target.value);
    postaviUnosEmailNovo(e.target.value)
  }
  const promjenaUnosaImeFilter = (e) => {
    postaviUnosImeFilter(e.target.value);

  }

  function filterItems(arr, query) {
    return arr.filter(function(el) {
        return el['ime'].toLowerCase().indexOf(query.toLowerCase()) !== -1
    })
  }
  
  const ukloniPoruku = (id) => {
    porukeServer
    .brisi(id)
    .then( response => {
      console.log(response);
      postaviPoruke(poruke.filter(p => p.id !== id))
    })
    .catch(error => {
      window.alert(error.response.data.error);
     })
  }

  const uredi = (id) => {

    const poruka = poruke.find(p => p.id === id)

    postaviidNovo(poruka.id)
    postaviUnosImeNovo(poruka.ime)
    postaviUnosEmailNovo(poruka.email)

  }
  const urediPoruku = () => {

    let id = idNovo;
    const novaPoruka = {
        id: id,
        ime: unosImeNovo,
        email: unosEmailNovo
        
    }
    porukeServer
    .osvjezi(id, novaPoruka)
    .then(
        (response) => {
            console.log(response);
            postaviPoruke(
                poruke.map(p => p.id !== id ? p : response.data) 
            )
        }
    )
    .catch(error => {
      window.alert(error.response.data.error);
     })
  }

  
    return (
      <div className="container row pt-4 mt-4">
        <div className="col-md-8">
          <h2 className="text-left text-secondary">ADRESAR</h2>
          <div className="form-group">
                  <input placeholder="Unesi ime za filtriranje..." value={unosImeFilter} onChange={promjenaUnosaImeFilter} id="imefilter" className="form-control text-right"></input>
          </div>
          <ul className="text-right list-group border border-secondary">
              {porukeZaIspis.map(p =>
                  <Poruka 
                  key={p.id} 
                  poruka={p} 
                  ukloni={
                    () => ukloniPoruku(p.id)
                  }
                  uredi={
                    () => uredi(p.id)
                  }
                  />
              )}        
          </ul>
          <div className="text-right float-right col-md-6 mr-0 pr-0">
            <form onSubmit={urediPoruku}>
                <input value={idNovo} onChange={promjenaidNovo} id="idnovo" className="form-control" hidden></input>
                <div className="form-group">
                  <input value={unosImeNovo} onChange={promjenaUnosaImeNovo} id="imenovo" className="form-control text-right"></input>
                  <input value={unosEmailNovo} onChange={promjenaUnosaEmailNovo} id="emailnovo" className="form-control text-right"/>
                  <button type='submit' className="btn btn-outline-secondary">Spremi promjene</button>
                </div>
            </form>
          </div>
        </div>
        <div className="col-md-4">
        <h2 id="sakrij">ADRESAR</h2>
          <form onSubmit={novaPoruka} className="card p-4 border-success"> 
          <legend className="card-title text-success">Novi kontakt</legend>
              <div className="form-group">
                <label htmlFor="ime">Ime i Prezime:</label>
                <input value={unosIme} onChange={promjenaUnosaIme} id="ime" className="form-control"/>
              </div>
              <div className="form-group">
                <label htmlFor="email">Email:</label>
                <input value={unosEmail} onChange={promjenaUnosaEmail} id="email" className="form-control"/>
              </div>
              <button type='submit' className="btn btn-success">Dodaj</button>
          </form>
        </div>
      </div>
    )
 }

export default App

